'use client';

import React, { useCallback, useEffect, useRef } from 'react';
import { useVoiceInteraction } from '../../../../hooks/useVoice/useVoiceInteraction';
import type { DeepgramVoiceInteractionProps } from './types';

/**
 * Next.js adapter for DeepgramVoiceInteraction
 */
export function DeepgramVoiceInteraction(props: DeepgramVoiceInteractionProps) {
  const {
    apiKey,
    transcriptionOptions,
    agentOptions,
    microphoneConfig,
    debug,
    onReady,
    onTranscriptUpdate,
    onAgentUtterance,
    onUserMessage,
    onAgentStateChange,
    onError
  } = props;

  const {
    start,
    stop,
    isReady,
    isRecording,
    agentState,
    error
  } = useVoiceInteraction({
    apiKey,
    transcriptionOptions,
    agentOptions,
    microphoneConfig,
    debug,
    onReady,
    onTranscriptUpdate,
    onAgentUtterance,
    onUserMessage,
    onAgentStateChange,
    onError
  });

  return null; // Headless component
};
